main{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100vh;
    object-fit: inherit;
    max-width: 70rem;
    object-position: center;
    padding-top: 10rem;
    padding-bottom: 3rem;
    border-radius: 20px;
    transition: ease 2s; 
}

section{
    text-align: center;
}

.home-content-container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
}

.introduction-con{
    display: flex;
    flex-direction: column;
    padding-top: 0.5rem;
    padding-left: 5px;
    padding-right: 5px;
}

.introduction-con h2{
    font-size: 2em;
}

.introduction-con p{
    font-size: 17px;
    font-weight: 600;
    margin-top: 1.5rem;
    font-style: italic;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

h4{
    padding: 15px;
}
blockquote{
    margin-top: 1.5rem;
}


.image-con {
    text-align: center;
    position: relative;
    overflow: none;
    background-image: url(./Imagestore/book-5211303_640.jpg);
    background-position: center;
    height: 70vh;
    background-repeat: no-repeat; 
  }
  
  .style-image{
    border-bottom-width: 20px;
    bottom: 0;
    background-color: white;
    position: absolute;
    height: 7vh;
    left: 0;
    right: 0;
    border-bottom-style: groove;
    border-top-style: dashed; 
    animation: change 7s linear infinite;
 }

 @keyframes change {
    0%{
        border-top-color: aliceblue;
    }

    10%, 20%{
        border-top-color: black; 
    }

    30%, 40%{
        border-top-color: rgb(6, 215, 187); 
    }

    50%, 60%{
        border-top-color: burlywood; 
    }

    70%, 80%{
        border-top-color: red; 
    }

   90%, 100%{
    border-top-color: green; 

   }
 }

 .style-image p{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 14px;
    animation: changes 5s linear infinite;

    font-style: oblique;
 }
  

 @keyframes changes {
    0%{
        color: aliceblue;
    }

    10%, 20%{
        color: black; 
    }

    30%, 40%{
        color: rgb(6, 215, 187); 
    }

    50%, 60%{
        border-top-color: burlywood; 
    }

    70%, 80%{
        color: red; 
    }

   90%, 100%{
    color: green; 

   }
 }
  

  

