.wole img{
    height: 70vh;
    width: 90%;
    object-position: center;
    object-fit: contain;
    position: absolute;
    left: 0;
    bottom: 0;
}


.image-con img{
    height: 70vh;
    width: 100%;
    object-position: center;
    object-fit: contain;
    position: absolute;
    left: 0;
}

.author{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-bottom: 4rem;
    padding-top: 1.5rem;
}

.author-name{
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 16px;
}


.author .getbook{
    padding: 13px 49px;
    font-size: 16px;
    border: none;
    text-decoration: none;
    color: white;
    border-radius: 10px;
    outline: none;
    background-color: brown;   
    cursor: pointer;
    margin-top: 1rem;
}

.getbook.changemode{
    color: white;
    transition: 0.3s ease;;
 
}



.mode{
    display: none;
    right: 10px;
    padding-top: 20px;
    
}

.social-media-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: -17.4px;
  }

  .social-media-link {
  font-size: 16px;
  padding: 9px 12px;
  border: none;
  text-decoration: none;
  text-align: center;
  color: white;
  border-radius: 10px;
  outline: none;
  background-color: brown;   
  cursor: pointer;
}
.social-media-container a{
    margin-left: 20px;
}




@media screen and (max-width: 768px){
    .home-content-container{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        row-gap: 2rem;
    }

    .author{
        display: flex;
        flex-direction: column;
        row-gap: 2rem;
    }
    
}